* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

:root {
  --white: #fff;
  --lightgreen: #3cba92;
  --purple: #5b127d;
  --pink: #f62854;
  --orange: #ffad5c;
  --blue: #0070e0;
  --lightgray:#ebebff;
  --brown: #301b2c;
}
 
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 
    sans-serif; 
}